import React, { Component } from "react";
import ProfileService from "./ProfileService";

class PaymentList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.id,
      paymentList: [],
      selectedStoreId: null, //   ID 
      buyList: [], //   
    };
  }

  componentDidMount() {
    this.loadPaymentList();
  }

  loadPaymentList() {
    ProfileService.getPaymentList(this.props.id)
      .then((response) => {
        this.setState({ paymentList: response.data });
      })
      .catch((error) => {
        console.error("     : ", error);
      });
  }

  //   :       
  handleStoreClick(storeId) {
    ProfileService.getBuyList(storeId)
      .then((response) => {
        this.setState({ buyList: response.data, selectedStoreId: storeId });
      })
      .catch((error) => {
        console.error("     : ", error);
      });
  }

  render() {
    const { paymentList, selectedStoreId, buyList } = this.state;

    return (
      <div className="payment-list">
        <h3 className="mb-4"> </h3>
        <table class="table table-hover">
          <thead>
            <tr class="table-primary">
              <th scope="row"></th>
              <th scope="row"></th>
              <th scope="row"></th>
              <th scope="row"></th>
            </tr>
          </thead>
          <tbody>
            {paymentList.map((payment, index) => (
              <tr key={index}>
                <td>
                  {/*           */}
                  <a onClick={() => this.handleStoreClick(payment.id)}>{payment.storeName}</a>
                </td>
                <td>{payment.storeAddress}</td>
                <td>{payment.price}</td>
                <td>
                  {payment.status === "waiting"
                    ? ""
                    : payment.status === "completed"
                    ? ""
                    : ""}
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {/*      */}
        {selectedStoreId && (
          <div>
            <h3 className="mt-4"> </h3>
            <table className="table table-hover">
              <thead>
                <tr class="table-primary">
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {buyList.map((item, index) => (
                  <tr key={index}>
                    <td>{item.menuName}</td>
                    <td>{item.quantity}</td>
                    <td>{item.menuPrice}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    );
  }
}

export default PaymentList;
